import React, { useEffect, useState } from 'react';
import './App.css';
import { Button, Container, Grid, GridColumn, Popup, Segment, Table, Dropdown, Divider, Form } from 'semantic-ui-react';
import { Formik, FormikErrors, useFormikContext } from 'formik';
import { MyForm } from './components/Form/MyForm';
import { MyInput } from './components/Form/MyInput';
import { calcChakras, calcLifeMap, calcOthers, calcOthers2, calcOthers3, currentDate, suggestNames } from './logic/Numerology';
import { ChakraRowProps, ChakraTableProps, MainFormProps, OtherFormValues, PersonInfo, HistoryDropDownProps, LifeMapRowProps, LifeMapCellProps, LifeMapTableProps } from './models/Interfaces';
import { MySelect } from './components/Form/MySelect';

const validateMainForm = (values: OtherFormValues & PersonInfo) => {
  let errors: FormikErrors<OtherFormValues & PersonInfo> = {};
  if (!values.birthDate) {
    errors.birthDate = " ";
  } else if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(values.birthDate.trim())) {
    errors.birthDate = "  ";
  }
  if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(values.cosmoDate.trim())) {
    errors.cosmoDate = "  ";
  }

  return errors;
};

type HistoryState = {[key: string]: PersonInfo};

const addToHistory = (elem: PersonInfo, histState: HistoryState, setHistState: React.Dispatch<React.SetStateAction<HistoryState>>) => {
  histState[elem.firstName+elem.lastName+elem.birthDate] = elem;
  setHistState(histState);
};

const getHistory = () => {
  const historyStr = localStorage.getItem("MainFormHistory");
  let history: HistoryState = {};
  if (historyStr !== null) {
    history = JSON.parse(historyStr);
  }
  return history;
};

const MainForm = (props: MainFormProps & {histState: HistoryState, setHistState: React.Dispatch<React.SetStateAction<HistoryState>>, formValues: OtherFormValues & PersonInfo, setFormValues: React.Dispatch<React.SetStateAction<OtherFormValues & PersonInfo>>}) => {
  const setFormValues = props.setFormValues;
  const histState = props.histState;
  useEffect(() => {
    const setHistory = () => {
      localStorage.setItem("MainFormHistory", JSON.stringify(histState));
    };
    return setHistory;
  });
  return (
    <Formik
      initialValues={props.formValues}
      validate={validateMainForm}
      onSubmit={(values, actions) => {
        console.log(values, actions);
        props.setFormValues(values);
        addToHistory(values, props.histState, props.setHistState);
        actions.setSubmitting(false);
      }}
    >
      {(props) => {
        return (
          <MyForm size="small" >
            <HistoryDropDown setFormValues={setFormValues} histState={histState}/>
            <Divider/>
            <MyInput style={{backgroundColor: "#ffffff"}} label=" " name="firstName"></MyInput>
            <MyInput style={{backgroundColor: "#ffffff"}} label=" " name="lastName"></MyInput>
            <MyInput style={{backgroundColor: "#ffffff"}} label=" " name="birthDate"></MyInput>
            <MyInput style={{backgroundColor: "#ffffff"}} label={currentDate()} name="cosmoDate"></MyInput>
            <Form.Group>
              <MyInput style={{backgroundColor: "#ffffff"}} label="" name="wantedThroat"></MyInput>
              <MyInput  style={{backgroundColor: "#ffffff"}}label=" " name="wantedCreation"></MyInput>
            </Form.Group>
            <MySelect name="gender" style={{height: 35, backgroundColor: "#ffffff"}}>
              <option value="male"></option>
              <option value="female"></option>
            </MySelect>
            <Button type="submit" disabled={props.isSubmitting} size="large" style={{backgroundColor: "#ffffff22"}}>
                  
            </Button>
            <Button type="reset" onClick={()=>document.location.reload()}size="large" style={{backgroundColor: "#ffffff22"}}>
                  
            </Button>
          </MyForm>
        );
      }}
    </Formik>
  );
};

const ChakraRow = (props: ChakraRowProps) => {
  let fw:"normal" | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | (number & {}) | "bolder" | "lighter" | undefined = 'normal';
  if (props.boldName){
    fw = 'bold';
  }
  return (
    <Table.Row>
      <Table.Cell><div style={{fontWeight: fw}}>{props.name}</div></Table.Cell>
      <Table.Cell>
        <Popup
          size="large"
          content={props.text}
          on="click"
          trigger={<div style={{color: props.textColor, backgroundColor: props.bgColor, textAlign: "center", border: "1px solid black", width: 95, height: 30}}><span dir="ltr" style={{verticalAlign: "middle"}}>{props.value}</span></div>}
          position="left center"
          disabled={props.text === undefined}
          style={{textAlign: "right", direction: "rtl"}}
          />
      </Table.Cell>
    </Table.Row>
  );
};

const ChakraTable = (props: ChakraTableProps&{padded: boolean, size?: 'small' | 'large'}) => {
  return (
    <Table size={props.size || "large"} padded={props.padded} collapsing celled textAlign="right" style={{backgroundColor: "#ffffff22"}}>
      <Table.Body>
        {props.chakras && props.chakras.map((chakra: ChakraRowProps) => <ChakraRow key={chakra.name} {...chakra}/>)}
      </Table.Body>
    </Table>
  );
};

const NamesTable = (props: {names?: Array<string>}) => {
  return (
    <Grid columns={3}>
      {props.names && props.names.map(name => <Grid.Column style={{color: "black"}}>{name}</Grid.Column>)}
    </Grid>
  );
};

const LifeMapCell = (props: LifeMapCellProps) => {
  return (
    <Table.Cell>
      <div style={{color: props.textColor, backgroundColor: props.bgColor, textAlign: "center", border: "1px solid black", width: 85, height: 26}}><span style={{verticalAlign: "middle"}}>{props.value}</span></div>
    </Table.Cell>
  );
};

const LifeMapRow = (props: LifeMapRowProps) => {
  return (
    <Table.Row>
      <LifeMapCell {...props.age}/>
      <LifeMapCell {...props.cycle}/>
      <LifeMapCell {...props.peak}/>
      <LifeMapCell {...props.challenge}/>
    </Table.Row>
  );
};

const LifeMapTable = (props: LifeMapTableProps) => {
  return (
    <Table size="small" textAlign="center" collapsing style={{backgroundColor: "#ffffff22"}}>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}></Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}></Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}></Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}></Table.HeaderCell>
        </Table.Row>
      </Table.Header>
      <Table.Body>
        {props.values && props.values.map((row: LifeMapRowProps, index: number) => <LifeMapRow key={index} {...row}/>)}
      </Table.Body>
    </Table>
  );
};

const HistoryElement = (props: PersonInfo & HistoryDropDownProps & {setValues: (values: unknown, shouldValidate?: boolean | undefined) => void}) => {
  const combined = props.firstName + " " + props.lastName + " " + props.birthDate;
  return {
    key: combined,
    value: combined,
    text: (
      <div
        style={{textAlign: "right"}}
      >
        {combined}
      </div>
    ),
    onMouseDown: (() => {
      props.setValues({
        firstName: props.firstName,
        lastName: props.lastName,
        birthDate: props.birthDate,
        cosmoDate: currentDate(),
        wantedThroat: "",
        wantedCreation: "",
        gender: "male",
      });
      props.setFormValues({
        firstName: props.firstName,
        lastName: props.lastName,
        birthDate: props.birthDate,
        cosmoDate: currentDate(),
        wantedThroat: "",
        wantedCreation: "",
        gender: "male",
      });
    }),
  };
};

const HistoryDropDown = (props: HistoryDropDownProps & {histState: HistoryState}) => {
  let history: PersonInfo[] = Object.values(props.histState);
  const { setValues } = useFormikContext();
  return (
    <Dropdown
      placeholder=""
      search={(options, value) => {return options.filter((elem) => {return elem.value?.toString().includes(value)})}}
      selection
      fluid
      style={{textAlign: "right", backgroundColor: "#ffffff"}}
      options={history.map((elem) => {
        return HistoryElement({
          firstName: elem.firstName,
          lastName: elem.lastName,
          birthDate: elem.birthDate,
          setValues: setValues,
          setFormValues: props.setFormValues
        })
      })}
    />
  );
};

function App() {
  const [histState, setHistState] = useState(getHistory());
  const [formValues, setFormValues] = useState({firstName: "", lastName: "", birthDate: "", cosmoDate: currentDate(), wantedThroat: "", wantedCreation: "", gender: "male"});
  return (
    <Container dir="rtl" style={{width: "100%"}} textAlign={"center"}>
      <Segment size="small" padded inverted tertiary color="violet">
        <Grid columns={3} stackable textAlign={"center"}>
          <Grid.Column width={3}>
            <MainForm formValues={formValues} setFormValues={setFormValues} histState={histState} setHistState={setHistState}/>
            <Divider hidden/>
            <NamesTable names={suggestNames(formValues.firstName, formValues.lastName, parseInt(formValues.wantedCreation)||null, parseInt(formValues.wantedThroat)||null, formValues.gender)}/>
          </Grid.Column>
          <Divider hidden/>
          <GridColumn width={6}>
            <LifeMapTable values={calcLifeMap(formValues.birthDate)}/>
            <Divider hidden/>
            <Grid columns={2}>
              <Grid.Column>
                <ChakraTable size="small" padded={false} chakras={calcOthers2(formValues.lastName, formValues.birthDate, formValues.cosmoDate)}/>
                <Divider hidden/>
                <ChakraTable size="small" padded={false} chakras={calcOthers3(formValues.lastName, formValues.birthDate, formValues.cosmoDate)}/>
              </Grid.Column>
              <Grid.Column>
                <ChakraTable size="small" padded={false} chakras={calcOthers(formValues.lastName, formValues.birthDate, formValues.cosmoDate)}/>
              </Grid.Column>
            </Grid>
          </GridColumn>
          <Divider hidden/>
          <Grid.Column width={4}>
            <ChakraTable padded={true} chakras={calcChakras(formValues.firstName, formValues.lastName, formValues.birthDate)}/>
          </Grid.Column>
        </Grid>
      </Segment>
    </Container>
  );
}

export default App;
