const calcCrownChakra = (birthDate: string) => {
    const crownChakraVal = calcDay(birthDate);
    let prefix = '';
    const day = birthDate.split('/')[0];
    if (parseInt(day) !== crownChakraVal) {
        prefix = day + '=';
    }
    const { color, backgroundColor } = getStyleForCell(crownChakraVal, "crownChakra");
    return {
        name: " ",
        value: prefix + showKarmaticNumber(crownChakraVal),
        text: textForChakra.crownChakra[crownChakraVal],
        textColor: color,
        bgColor: backgroundColor,
    };
};

