const calcHiddenPersonalYear = (birthDate: string, cosmoDate: string) => {
    const age = calcBirthday(birthDate, cosmoDate);
    const personalYearValue = calcDate(age.lastBirthday);
    const sunChakraVal = calcDate(birthDate);
    const hiddenPersonalYear = calcNum(personalYearValue + sunChakraVal);
    return {
        name: "  ",
        value: showKarmaticNumber(hiddenPersonalYear),
        textColor: "black",
        bgColor: "white",
    };

};

const calcAgeForLifeMap = (date: string, cyc: 1|2|3|4) => {
    const sunChakra = calcNum(calcDate(date), true);
    if (cyc === 1) return "0-" + (36 - sunChakra).toString();
    else if (cyc === 2) return (37 - sunChakra).toString() + "-" + (36 - sunChakra + 9).toString();
    else if (cyc === 3) return (37 - sunChakra + 9).toString() + "-" + (36 - sunChakra + 18).toString();
    else return (37 - sunChakra + 18).toString() + "-120";
};

const calcCycle = (date: string, cyc: 1|2|3|4) => {
    const splitted = date.split('/');
    let day = parseInt(splitted[0]), month = parseInt(splitted[1]), year = parseInt(splitted[2]);
    if (shouldReduce(day)) {
        day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(year)) {
        year = calcNum(splitted[2].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (cyc === 1){
        return month;
    }
    if (cyc === 2 || cyc === 3){
        return day;
    }
    if (cyc === 4){
        return year;
    }
    return -1;
};

const calcChallenge = (date: string, chall: 1|2|3|4) => {
    const splitted = date.split('/');
    let day = parseInt(splitted[0]), month = parseInt(splitted[1]), year = parseInt(splitted[2]);
    if (shouldReduce(day)) {
        day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(year)) {
        year = calcNum(splitted[2].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    const reducedDay = calcNum(day, true);
    const reducedMonth = calcNum(month, true);
    const reducedYear = calcNum(year, true);
    if (chall === 1) {
        let result = reducedDay - reducedMonth;
        if (result < 0) result = -result;
        return result;
    }
    if (chall === 2) {
        let result = reducedDay - reducedYear;
        if (result < 0) result = -result;
        return result;
    }
    if (chall === 3) {
        let result1 = reducedDay - reducedMonth;
        if (result1 < 0) result1 = -result1;
        let result2 = reducedDay - reducedYear;
        if (result2 < 0) result2 = -result2;
        let result = result1 - result2;
        if (result < 0) result = -result;
        return result;
    }
    if (chall === 4) {
        let result = reducedMonth - reducedYear;
        if (result < 0) result = -result;
        return result;
    }
    return -1;
};

const calcLifeMapRow = (birthDate: string, row: 1|2|3|4): LifeMapRowProps => {
    const ageValue = calcAgeForLifeMap(birthDate, row);
    const cycleValue = calcCycle(birthDate, row);
    let cycleStyle;
    if (row === 1) {
        cycleStyle = getStyleForCell(cycleValue, "cycle1");
    } else {
        cycleStyle = getStyleForCell(cycleValue, "cycle");
    }
    const challengeValue = calcChallenge(birthDate, row);
    const challengeStyle = getStyleForCell(challengeValue, "challenge");
    const peakValue = calcPeak(birthDate, row);
    const peakStyle = getStyleForCell(peakValue, "peak");
    return {
        age: {
            value: ageValue,
            textColor: "black",
            bgColor: "white",
        },
        cycle: {
            value: showKarmaticNumber(cycleValue),
            textColor: cycleStyle.color,
            bgColor: cycleStyle.backgroundColor,
        },
        challenge: {
            value: showKarmaticNumber(challengeValue),
            textColor: challengeStyle.color,
            bgColor: challengeStyle.backgroundColor,
        },
        peak: {
            value: showKarmaticNumber(peakValue),
            textColor: peakStyle.color,
            bgColor: peakStyle.backgroundColor,
        },
    };
}

export const calcLifeMap = (birthDate: string): Array<LifeMapRowProps> => {
    const value = "";
    const textColor = "black";
    const bgColor = "white";
    const empty = {value, textColor, bgColor};
    const emptyLifeMApRow = {
        age: empty, cycle: empty, challenge: empty, peak: empty
    }
    const emptyLifeMap = [emptyLifeMApRow, emptyLifeMApRow, emptyLifeMApRow, emptyLifeMApRow]
    return (birthDate && [
        calcLifeMapRow(birthDate, 1),
        calcLifeMapRow(birthDate, 2),
        calcLifeMapRow(birthDate, 3),
        calcLifeMapRow(birthDate, 4),
    ]) || emptyLifeMap;
};

